<script>
/* eslint-disable no-new */
import bus from "@/bus.js";
import PerfectScrollbar from "perfect-scrollbar";
import "perfect-scrollbar/css/perfect-scrollbar.css";

function hasElement(className) {
  return document.getElementsByClassName(className).length > 0;
}

function initScrollbar(className) {
  if (hasElement(className)) {
    new PerfectScrollbar(`.${className}`);
  } else {
    // try to init it later in case this component is loaded async
    setTimeout(() => {
      initScrollbar(className);
    }, 100);
  }
}

// import DashboardNavbar from "@/tkViews/Pages/DashboardNavbar.vue";
import { SlideXRightTransition } from "vue2-transitions";
import SidebarItem from "@/tkViews/components/SidebarPlugin/SidebarItem";
import FullLoading from "../components/layout/FullLoading.vue";
import MyProfile from '@/tkViews/components/Modal/MyProfile.vue';

export default {
  data() {
    return {
      // ready: false,
      ready: true,
      showSearchForm: false,
      userId: "", // 用户 id
      name: "Sophie Snow", //  用户名
      avatar:
        "https://demos.creative-tim.com/vue-argon-dashboard-pro//img/theme/team-4.jpg", // 用户头像
      showProfile: false
    };
  },
  components: {
    // DashboardNavbar,
    // FadeTransition,
    // SlideXLeftTransition,
    SlideXRightTransition,
    SidebarItem,
    FullLoading,
    MyProfile,
  },
  methods: {
    initScrollbar() {
      let isWindows = navigator.platform.startsWith("Win");
      if (isWindows) {
        initScrollbar("sidenav");
      }
    },
    logout() {
      console.log("click log out");
      bus.$emit("showLoading");
      this.$firebase
        .auth()
        .signOut()
        .then(function () {
          console.log("Sign-out successful.");
          localStorage.clear();
          bus.$emit("hideLoading");
        })
        .catch(function (error) {
          console.log("Sign-out failed: ", error);
        });
    },
  },
  created() {
    console.log("dashboard layout");
  },
  mounted() {
    this.initScrollbar();
    bus.$on("showLoading", () => {
      this.ready = false;
    });

    bus.$on("hideLoading", () => {
      this.ready = true;
    });

    bus.$on("showProfile", () => {
      this.showProfile = true;
    });
  },
};
</script>
