function isEmail(email) {
  let reg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
  if (reg.test(email)) {
    return true;
  } else {
    return false;
  }
}

function isNull(obj) {
  return (obj === undefined || obj === null || obj === "")
}

function getFirstLetter(str) {
  let result = ""
  if (!isNull(str)) {
    let arr = str.split(" ")
    arr.some(item => {
      result += item.substring(0, 1).toString().toUpperCase()
    })
  }
  return result;
}

function getAvatarLineHeight(avatar) {
  switch (avatar) {
    case "":
      return 48
    case "xs":
      return 24
    case "sm":
      return 36
    case "lg":
      return 58
    case "xl":
      return 74
  }
}

function validImage(pathImg) {
  var ImgObj = new Image();
  ImgObj.src = pathImg;
  setTimeout(() => {
    console.log(ImgObj.fileSize, ImgObj.width, ImgObj.height)
    if (ImgObj.fileSize > 0 || (ImgObj.width > 0 && ImgObj.height > 0)) {
      return true;
    } else {
      return false;
    }
  }, 200)
}

