function isTeacher(roleIds) {
  console.log('检查用户身份: ', roleIds)
  if (!Array.isArray(roleIds)) {
    console.log("当前参数不是数组")
    return false
  }

  if (roleIds.indexOf('1') >= 0) {
    console.log('当前是老师')
    return true
  }
  if (roleIds.indexOf(1) >= 0) {
    console.log('当前是老师2')
    return true
  }
  console.log('当前不是老师')
  return false
}

function getInstrumentUrl(id) {
  let url = ""
  instruments.some(item => {
    if (item.id === id) {
      url = item.url
    }
  })
  return url
}

function formatYYMMDD(timestamp) {
  let d = new Date(timestamp)
  return (d.getFullYear() + "/" + (d.getMonth() + 1) + "/" + d.getDate())
}

function numberToPercent(number) {
  let num = parseFloat(number)
  let percent = (num * 100).toFixed(2)
  return percent
}

