import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const store = new Vuex.Store({

  state: { // 定义属性值
    isAllowedSignin: true
  },

  mutations: { // 设置方法函数，不能有异步处理
    toggleAllowedSignIn( state, boolValue ) {
      state.isAllowedSignin = boolValue
    }
  },

  actions: { // 用来处理 mutatuions 的异步处理

  },

  getters: { // 类似 vue 中的 computed, 用于基于 state 值 的计算
    
  }

})

export default store;